/*-------- Form --------*/

.form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    padding: 50px 36px;
    box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);

    background-color: #16336b;
    border-radius: 4px;

    &__button {
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
    }
}

.form {

    &--contact {
        max-width: 595px;
        margin-top: 72px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -110px;
        z-index: 2;
        position: relative;
    }
}

@media (max-width: 1219px) {

    .form {

        &--contact {
            margin-bottom: 50px;
        }
    }
}

@media (max-width: 1099px) {

    .form {

        &--contact {
            margin-top: 50px;
        }
    }
}

@media (max-width: 989px) {

    .form {
        padding: 30px;

        &__button {
            margin-top: 20px;
        }
    }
}

@media (max-width: 479px) {

    .form {
        padding: 20px;
    }
}
