/*-------- Case-studies --------*/

.case-studies {
    padding-bottom: 158px;

    &__banner {
        height: 733px;
        position: relative;
        overflow: hidden;
    }

    &__banner-wrapper {
        height: 100%;
        width: 100%;

        position: absolute;
        left: 0;
        top: 0;
    }

    &__banner-image {
        width: 100%;
        height: 100%;

        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__banner-image:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.3);
    }

    &__banner-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        z-index: 1;
        position: relative;
    }

    &__banner-title {
        font-family: $font-pf;
        font-size: 64px;
        color: #ffffff;
    }

    &__banner-arrow {
        width: 16px;
        height: 16px;

        position: absolute;
        bottom: 106px;

        border: 2px solid #ffffff;
        border-left: none;
        border-top: none;
        transform: rotateZ(45deg);
        cursor: pointer;
    }

    &__wrapper {
        padding-top: 88px;
        position: relative;   
    }

    &__wrapper-container {
        position: relative;
    }

    &__cards {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 1058px;

        margin-top: 142px;
        margin-left: auto;
        margin-right: auto;
    }

    &__cards-item {
        width: 49%;
        overflow: hidden;
    }

    &__cards-item:nth-child(n+3) {
        margin-top: 55px;
    }
}

@media (max-width: 1099px) {

    .case-studies {
        padding-bottom: 70px;

        &__banner {
            height: calc(100vh - 90px);
        }

        &__banner-title {
            font-size: 52px;
        }

        &__banner-arrow {
           bottom: 50px;
        }
    }
}

@media (max-width: 989px) {

    .case-studies {

        &__banner-title {
            font-size: 45px;
        }

        &__cards {
            margin-top: 80px;
        }
    }
}

@media (max-width: 767px) {

    .case-studies {
        padding-bottom: 20px;

        &__wrapper {
            padding-top: 50px;
        }

        &__line-text {
            font-size: 3.5vw;
        }

        &__cards {
            margin-top: 50px;
        }

        &__banner {
            height: 300px;
        }
    }
}

@media (max-width: 619px) {

    .case-studies {

        &__banner {
            height: 250px;
        }

        &__banner-title {
            font-size: 30px;
        }

        &__line-text {
            font-size: 3.4vw;
        }

        &__cards-item {
            width: 100%;
        }

        &__cards-item:not(:first-child) {
            margin-top: 30px;
        }

        &__banner-arrow {
            width: 10px;
            height: 10px;
        }
    }
}

@media (max-width: 479px) {

    .case-studies {

        &__line-text {
            font-size: 4.2vw;
        }
    }
}